<template>
    <!-- 个人中心 -->
    <div class="thnu_personal">
        <header class="container-fluid">
            <div class="container head_user">
                <img :src="userInfo.avatar" alt="">
                <span>{{userInfo.userName}}</span>
            </div>
            <div class="container head_nav ">
                <ul class="nav nav-pills" role="tablist">
                    <li role="presentation" class="active">
                        <a href="#home" aria-controls="home" role="tab" data-toggle="tab">个人资料</a>
                    </li>
                    <li role="presentation">
                        <a href="#profile" aria-controls="profile" role="tab" data-toggle="tab">我的消息</a>
                    </li>
                    <li role="presentation" class="dropdown">
                        <a class="dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-expanded="false">
                            更多信息
                            <span class="caret"></span>
                        </a>
                        <ul class="dropdown-menu" role="menu">
                            <li>
                                <a href="#">Action</a>
                            </li>
                            <li>
                                <a href="#">Another action</a>
                            </li>
                            <li>
                                <a href="#">Something else here</a>
                            </li>
                            <li class="divider"></li>
                            <li>
                                <a href="#">Separated link</a>
                            </li>
                        </ul>
                    </li>
                </ul>
                <br style="clear: both">
            </div>
        </header>
        <div role="tabpanel" class="container content">
            <div class="tab-content">
                <div role="tabpanel" class="tab-pane active" id="home">123</div>
                <div role="tabpanel" class="tab-pane" id="profile">456</div>
                <div role="tabpanel" class="tab-pane" id="messages">...</div>
                <div role="tabpanel" class="tab-pane" id="settings">...</div>
            </div>

        </div>
    </div>

</template>
<script>
export default {
    data() {
        return {
            userInfo: {
                userName: "haha",
                avatar: "static/images/ershou.jpg"
            }
        };
    }
};
</script>
<style lang="less" scoped>
header {
    // height: 200px;
    background: url(/static/images/personal.jpg)
        no-repeat center center;
    background-size: cover;
    .head_user {
        padding-top: 20px;
        img {
            display: block;
            width: 50px;
            height: 50px;
            border-radius: 50%;
        }
        span{
            font-size: 20px;
        }
    }
    .head_nav {
        padding-top: 30px;
        ul > li > a {
            color: black;
        }
        ul > li:hover > a {
            // color: black;
        }
    }
}

.content {
    padding-top: 20px;
}
</style>





