<template>
    <div class="my_goods">
        <div class="item">
            <h4>My Goods</h4>
            <ul v-for="item in myGoodsList" :key="item._id">
                <ul>
                    <li v-for="img in item.photo" :key="img._id" v-show="img">
                        <img :src="img" alt="">
                    </li>
                    <span>时间：{{item.date}}</span>
                    <p>内容：{{item.content}}</p>
                </ul>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            myGoodsList: [],
            goodsList: []
        };
    },
    created() {
        this.getMyGoods();
    },
    methods: {
        getMyGoods: function() {
            this.axios
                .get("/user/getGoodsList")
                .then(res => {
                    if (res.data.status == "success") {
                        this.goodsList = res.data.message;
                        this.myGoodsList = this.goodsList.reverse();
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        }
    }
};
</script>
<style lang="less" scoped>
.item {
    ul {
        list-style: none;
        padding: 10px 10px;
        background-color: #eee;
    }
    span {
        padding-top: 10px;
    }
    li > img {
        width: 150px;
        height: 180px;
    }
}
</style>



