<template>
    <div class="my_sport">
        <div class="item">
            <ul v-for="item in mySportList" :key="item._id">
                <ul>
                    <p>标题：{{item.title}}</p>
                    <p>发布时间：{{item.date}}</p>
                    <p>内容：{{item.content}}</p>
                </ul>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            mySportList: [],
            sportList: []
        };
    },
    created() {
        this.getMySport();
    },
    methods: {
        getMySport: function() {
            this.axios
                .get("/user/getSportList")
                .then(res => {
                    if (res.data.status == "success") {
                        this.sportList = res.data.message;
                        this.mySportList = this.sportList.reverse();
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        }
    }
};
</script>
<style lang="less" scoped>
.item {
    ul {
        list-style: none;
        padding: 10px 10px;
    }
    span {
        padding-top: 10px;
    }
}
</style>



